@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&family=Rozha+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}button{cursor:pointer;border-radius:0 0;border:none}html{font-size:125%;font-family:"Roboto",sans-serif;font-weight:300}.app{margin:0 75px;display:grid;grid-template-columns:65% 35%;grid-template-rows:100px 45px 55vh 1fr}h1,h3{font-family:"Rozha One",serif;font-size:3rem;line-height:80px;color:#2D0B94}h2,h4{font-size:3rem;font-weight:400;color:#2D0B94;width:400px}h4{font-size:2rem}h3{font-size:3rem;line-height:6rem}h5{display:none}h6{font-weight:400}.pageUp{position:fixed;bottom:20px;right:20px;height:45px;width:45px;font-family:"Rozha One",serif;color:#2D0B94;background-color:#F5E3E6}.pageUp a{font-size:0.75rem;line-height:45px;text-decoration:none}nav{grid-column:1/3;grid-row:1/3;display:flex;height:calc(80px + 45px);justify-content:space-between;margin-top:20px}nav button{margin:20px 25px 0 0;width:90px;height:calc(100% - 20px);position:relative;z-index:10;background-color:#E8312F;color:white}nav button:hover,nav button:active,nav button:focus{transform:scale(1.02);border:none;outline:none}nav button span{font-family:"Rozha One",serif;display:block;font-size:2.75rem;margin-top:-15px}nav button p{display:block;font-size:0.75rem;font-weight:600;width:70%;margin-left:15%;text-align:center}nav button p:nth-of-type(1){margin-top:-15px}header{width:100%;height:calc(57vh + 30px);background-color:#F5E3E6;background:linear-gradient(148deg, #fff 0%, #f5e3e6 50%);grid-column:1/3;grid-row:2/3}header div{height:100%;display:flex;flex-direction:row-reverse}header div .headerImg{height:55vh;width:480px;margin:20px -90px 0 0}header div .headerImg img{height:55vh;width:480px}header div .headerContent{display:flex;flex-direction:column;margin:90px 100px 0 0}header button{font-family:"Rozha One",serif;font-size:2rem;margin-top:40px;background:none;color:#2D0B94;border:3px solid #2D0B94}header button:hover{background-color:#E8312F;color:white;border:3px solid #E8312F}.howTo{height:calc(100% - 70px - 70px);color:#2D0B94;padding:70px}.howTo p{font-weight:400;margin:20px 0}.howTo li{margin-top:8px}.howTo button{padding:0 30px}aside{display:none;background-color:white}aside.istrue{display:block}.yourStackParent{margin:20px}.yourStackBook{display:flex;margin-top:20px}.yourStackBook .cover{width:70px;height:100px}.yourStackBook .cover img{width:70px;height:100px}.yourStackBook .details{margin-left:10px}.yourStackBook .details span{display:block;margin-top:4px}.yourStackBook .details button{display:inline-block;font-size:0.75rem;padding:4px;margin:10px 10px 0 0;width:70px;background:none;color:#000;border:1px solid #000}.yourStackBook .details button:hover{background-color:#E8312F;color:white;border:1px solid #E8312F}main{grid-column:1/3;grid-row:4/5}main.istrue{grid-column:1/2}.bestSellers{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-template-rows:repeat(1fr);grid-gap:25px;justify-items:center}.bestSellers .bestSellerBook{width:200px}.bestSellers .bestSellerBook h6{font-size:0.8rem;margin-top:4px}.bestSellers .bestSellerBook span{font-size:0.75rem}.bestSellerBook div{width:100%;height:300px;position:relative}.bestSellerBook div img{width:100%;height:100%}.bestSellerBook div .stackButton{font-family:"Rozha One",serif;font-size:1.75rem;line-height:3rem;width:100%;display:none;position:absolute;bottom:0;left:0;background-color:#E8312F;color:white;box-shadow:0px -5px 7px -3px rgba(0,0,0,0.75)}.bestSellerBook:hover .stackButton{display:block}.bestSellerBook section{display:none}.getDetails{background:none;color:#000;border:1px solid #000;display:block;width:60px;margin-top:5px;font-size:0.65rem;font-weight:300}.getDetails:hover{background-color:#E8312F;color:white;border:1px solid #E8312F}.getDetails:hover{background-color:#F5E3E6;border:1px solid #F5E3E6;color:#2D0B94;font-weight:400}.bookDetail{grid-column:1/-1;display:flex;justify-content:flex-end;width:100%;background-color:#F5E3E6;background:linear-gradient(148deg, #fff 30%, #f5e3e6 90%)}.bookDetail img{width:400px}.subBookListNames{margin:10px 0 50px 0}.subBookListNames button{font-size:1rem;font-weight:600;padding:5px 10px;margin:5px 5px;background:none;color:#2D0B94;border:3px solid #2D0B94}.subBookListNames button:hover{background-color:#E8312F;color:white;border:3px solid #E8312F}.final{display:block;width:100%;color:#2D0B94;line-height:5rem;text-align:center}@media (min-width: 1031px){nav button.istrue{width:calc(35% - 25px)}nav button.istrue span,nav button.istrue p{display:none}nav button.istrue h5{display:block;font-family:"Rozha One",serif;font-size:2rem}aside{grid-column:2/3;grid-row:3/5;width:calc(100% - 25px);box-shadow:0px 3px 20px -8px rgba(0,0,0,0.75)}}@media (max-width: 1030px){.app{margin:0 45px}h2{font-size:2rem;width:300px}header div .headerContent{margin:130px 50px 0 0}header div .headerContent button{font-size:1.5rem}nav button.istrue span,nav button.istrue p{display:none}nav button.istrue h5{display:block;font-family:"Rozha One",serif;font-size:2rem}aside{grid-column:1/3;grid-row:4/5}main.istrue{display:none}}@media (max-width: 830px){header div{justify-content:center}.headerImg{display:none}.howTo{height:calc(100% - 30px - 30px);padding:30px}.howTo li{margin-top:8px}}@media (max-width: 650px){.app{margin:0 15px;grid-template-rows:100px 45px 67vh 1fr}nav button{position:absolute;top:calc(80px + 30px + 63vh);left:15%;height:80px;width:70%;margin-top:0}nav button p{width:100%;margin:0}.pageUp{bottom:10px;right:10px}}

